import { Observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { CanMutate } from '../../../atoms/CanMutate'
import { CanQuery } from '../../../atoms/CanQuery'
import { useRoot } from '../../../atoms/RootProvider'
import { i18n } from '../../../i18n'
import { PageLayout } from '../../../molecules/PageLayout'
import { PageLayoutFullHeightItem } from '../../../molecules/PageLayoutFullHeightItem'
import { PageLayoutItem } from '../../../molecules/PageLayoutItem'
import { CreateOrderButton } from '../../main/molecules/CreateOrderButton'
import { BranchDefaultTime } from '../organisms/BranchDefaultTime'
import { MapFullscreenButton } from '../organisms/MapFullscreenButton'
import { OrderList } from '../organisms/OrderList'
import { OrderListBranchFilter } from '../organisms/OrderListBranchFilter'
import { OrderMap } from '../organisms/OrderMap'
import { StatsFullscreenButton } from '../organisms/StatsFullscreenButton'

function getLayout() {
  return [
    { i: 'filter', x: 0, y: 1, w: 6, h: 1 },
    { i: 'neworder', x: 6, y: 1, w: 2, h: 1 },
    { i: 'headcontainer', x: 4, y: 0, w: 8, h: 1 },
  ]
}

const FilterContainer = styled.div`
  margin: -0.05rem 0rem -0.05rem -0.18rem;
`
const TopBarContainer = styled.div`
  display: flex;
`

export const OrderOverviewPage: React.FC = () => {
  const { user } = useRoot()
  return (
    <>
      <Observer>
        {() => (
          <PageLayoutFullHeightItem
            withScrollArea={true}
            row={user.hasSingleBranch ? 1 : 2}
            widthInColumns={8}
            scrollAreaId="order-list"
          >
            <OrderList />
          </PageLayoutFullHeightItem>
        )}
      </Observer>

      <PageLayoutFullHeightItem
        widthInColumns={8}
        column={8}
        withScrollArea={false}
      >
        <OrderMap />
        <MapFullscreenButton />
      </PageLayoutFullHeightItem>

      <PageLayout
        layouts={getLayout()}
        pageTitle={i18n.t`Orders`}
        showSoundControl={true}
        scrollPageTitle={false}
        topContent={
          <TopBarContainer>
            <CanMutate mutationName="updateDefaultOrderProcessTime">
              <BranchDefaultTime />
            </CanMutate>
            <CanQuery queryName="statistics">
              <StatsFullscreenButton />
            </CanQuery>
          </TopBarContainer>
        }
      >
        <PageLayoutItem
          key="filter"
          component={() => (
            <FilterContainer>
              <OrderListBranchFilter />
            </FilterContainer>
          )}
        />
        <PageLayoutItem
          key="neworder"
          component={() => (
            <CanMutate mutationName="createOrder">
              <CreateOrderButton showOnlyText={true} />
            </CanMutate>
          )}
        />
      </PageLayout>
    </>
  )
}
